<template>
  <!-- 人像部分 -->
  <div class="face-wrap cw-video">
    <div class="tip-face">
      <div class="img-wrap">
        <slot name="imgSlot"></slot>
        <img
          class="default-img"
          v-if="showDefaultImg"
          src="~@/assets/imgs/default.png"
          alt=""
        />
        <div class="loading-wrap" v-if="loading">
          <img class="loading-img" src="~@/assets/imgs/loading.gif" alt="" />
        </div>
      </div>
    </div>
    <van-circle
      class="circle"
      v-model="currentRate"
      :color="gradientColor"
      :rate="rate"
      size="57vw"
      :clockwise="false"
      :stroke-width="16"
      layer-color="#c9c9c9"
    />
    <slot></slot>
  </div>
</template>

<script></script>

<script>
export default {
  props: {
    loading: {
      type: Boolean,
      default: false,
    },
    showDefaultImg: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      currentRate: 0,
      rate: 100,
      gradientColor: {
        "0%": "#607efd",
        "100%": "#38cbb3",
      },
    };
  },
  methods: {
    init() {
      this.rate = 100;
    },
    start(time = 5) {
      const split = time / 12.5;
      const timmer = setInterval(() => {
        this.rate = this.rate - split;

        if (this.rate <= 0) {
          this.rate = 0;
          clearInterval(timmer);
          this.$emit("timeEnd");
        }
      }, 20);
    },
  },
};
</script>
<style lang="less" scoped>
.face-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  .circle {
    position: absolute;
  }
  .tip-face {
    width: 468px;
    height: 468px;
    border: 2px solid rgba(185, 193, 232, 0.3);
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    .img-wrap {
      width: 420px;
      height: 420px;
      position: relative;
      border-radius: 50%;
      overflow: hidden;
      .loading-wrap {
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 50%;
        background: rgba(53, 55, 65, 0.75);
        .loading-img {
          width: 120px;
        }
      }
    }
    .default-img {
      width: 100%;
      height: 100%;
    }
  }
}
</style>
