<template>
  <div class="result">
    <!-- <van-nav-bar title="" :border="false" left-arrow @click-left="onClickLeft" /> -->
    <!-- 内容 -->
    <div class="tip-title">验证成功</div>
    <!-- 人像部分 -->
    <cw-video class="cw-video">
      <div class="success-img">
        <img src="~@/assets/imgs/success.png" alt="" />
      </div>
      <template #imgSlot>
        <img class="default-img" :src="successImg" alt="" />
      </template>
    </cw-video>
  </div>
</template>

<script>
import CwVideo from '@/components/cw-video/index.vue'

export default {
  components: {
    CwVideo
  },
  data() {
    return {
      successImg: ''
    }
  },
  async mounted() {
    const result = JSON.parse(localStorage.getItem('result') || '{}')
    if (result.result) {
      this.successImg = result.image
      const isAlipay = navigator.userAgent.indexOf('AliApp') > -1 // 是否支付宝环境
      console.log('isAlipay', isAlipay)
      let globalEnv
      try {
        // eslint-disable-next-line no-undef
        globalEnv = isAlipay ? my : wx.miniProgram // h5 with 小程序通讯，获取实例对象
      } catch (error) {
        console.log(error)
      }
      setTimeout(() => {
        console.warn('跳转成功的url')
        const matchString = 'base64,'
        const index = result.image.indexOf(matchString)
        if (index === -1) return '匹配不到图片地址'
        const img = result.image.substr(index + matchString.length)
        globalEnv.postMessage({ data: img })
        globalEnv.navigateBack({ delta: 1 })

        // const url = window.config.successUrl
        // location.href = url
        // this.$router.go(-1)
      }, window.config.successDelayTime)
    }
  },
  methods: {
    onClickLeft() {
      console.warn('点击了返回键盘')
      // 返回功能
      const url = window.config.returnUrl
      location.href = url
    }
  }
}

// 成功页
</script>

<style lang="less" scoped>
.result {
  .tip-title {
    font-size: 36px;
    color: #333742;
    text-align: center;
    line-height: 36px;
    font-weight: 400;
    padding-top: 120px;
    padding-bottom: 60px;
  }
  .cw-video {
    .success-img {
      width: 120px;
      height: 120px;
      border-radius: 50%;
      position: absolute;
      z-index: 1000;
      right: 150px;
      bottom: 20px;
      background: #52ccb8;
      display: flex;
      align-items: center;
      justify-content: center;
      img {
        width: 46px;
      }
    }
    .default-img {
      width: 100%;
      height: 100%;
      transform: rotateY(180deg);
    }
  }
}
</style>
